* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --color-primary: #5ec576;
  --color-secondary: #ffcb03;
  --color-tertiary: #ff585f;
  --color-primary-darker: #4bbb7d;
  --color-secondary-darker: #ffbb00;
}
body {
  font-family: sans-serif;
  color: #444;
  background-color: #fff;
  overflow-x: hidden;
}

header {
  display: grid;
  place-items: center;
  height: 20vh;
}
header h1 {
  font-size: 40px;
}

.btn {
  background-color: var(--color-primary);
  border: none;
  padding: 15px;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s;
  width: 150px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
}
.btn:hover {
  background-color: var(--color-primary-darker);
}
button:focus {
  outline: none;
}

.operations {
  max-width: 1440px;
  margin: 2rem 0 0;
  padding-bottom: 2rem;
  background-color: #f3f3f3;
  min-height: 70vh;
}
.operations__tab-container {
  display: flex;
  justify-content: center;
}
.operations__tab {
  margin-right: 1.5rem;
  transform: translateY(-50%);
}
.operations__tab--1 {
  background-color: var(--color-secondary);
}
.operations__tab--1:hover {
  background-color: var(--color-secondary-darker);
}
.operations__tab--active {
  transform: translateY(-66%);
}
.operations__content {
  text-align: center;

  display: none;
}
.operations__content--active {
  display: block;
}

form {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px auto;
}
select {
  appearance: none;
  padding: 10px;
  cursor: pointer;
  border-radius: 5px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
  outline: none;
  border: none;
}
select:hover {
  background-color: #dddddd;
}
select:active {
  outline: none;
  border: none;
}
.select--city {
  padding: 10px;
}
.search-input {
  width: 400px;
  padding: 10px;
  border: none;
  margin-left: 5px;
  border-radius: 5px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
}
input:focus {
  outline: none;
}
.bookmark {
  fill: gray;
  height: 25px;
  cursor: pointer;
}
.bookmark--active {
  fill: var(--color-primary-darker);
}
.btn-page {
  padding: 10px;
  cursor: pointer;
  background-color: var(--color-primary);
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s;
  width: 100px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
}
.btn-page:hover {
  background-color: var(--color-primary-darker);
}

table {
  border-collapse: collapse;
  margin: 25px 0;
  font-size: 0.9em;
  width: 100%;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
  background-color: #fff;
  table-layout: fixed;
}
table thead tr {
  background-color: #009879;
  color: #ffffff;
}

table th,
table td {
  padding: 10px 5px;
}

table tbody tr {
  border-bottom: 1px solid #dddddd;
}

table tbody tr:nth-of-type(even) {
  background-color: #f3f3f3;
}

table tbody tr:last-of-type {
  border-bottom: 2px solid #009879;
}
